* {
  margin: 0;
  font-family: Poppins;
}

a {
  text-decoration: none;
}

.header {
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #eceef0;
  justify-content: center;
  padding: 22px 0;
  display: flex;
  position: fixed;
}

.container {
  width: 1200px;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.logo {
  align-items: start;
  margin-right: 200px;
  display: flex;
  position: relative;
}

.logo__headline {
  color: #0ac963;
  margin-top: 3px;
  font-size: 22px;
  font-weight: 600;
}

.logo__description {
  color: #888;
  font-size: 10px;
  font-weight: 500;
  position: absolute;
  top: 28px;
  left: 67px;
}

.searchbar {
  display: flex;
  position: relative;
}

.searchbar__input {
  color: #888;
  width: 300px;
  border: 1px solid #bdc5cc;
  border-radius: 5px;
  outline: none;
  padding: 9px 10px;
  font-size: 13px;
  font-weight: 400;
}

.searchbar__input:focus {
  border: 1px solid #0ac963;
}

.button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  position: absolute;
  top: 11px;
  right: 6px;
}

.icon {
  height: 15px;
}

.main {
  background-color: #eceff1;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding-top: 120px;
  display: flex;
}

.gallery {
  width: 1200px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 50px;
  display: flex;
}

.photo-card {
  width: 273px;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo-card__image {
  width: 250px;
  height: 167px;
  border-radius: 10px;
  margin-top: 10px;
}

.info {
  margin-top: 15px;
  margin-bottom: 10px;
  display: flex;
}

.info-box {
  color: #888;
  flex-direction: column;
  align-items: center;
  font-size: 11px;
  display: flex;
}

.info-box:not(:last-child) {
  margin-right: 36px;
}

.info-item {
  margin-top: 7px;
}

.hide {
  display: none;
}

.load-more {
  color: #0ac963;
  width: 140px;
  height: 40px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-bottom: 50px;
  font-size: 14px;
  font-weight: 500;
  transition: all .25s cubic-bezier(.175, .885, .32, 1.275);
}

.load-more:-webkit-any(:hover, :focus) {
  color: #fff;
  background-color: #0ac963;
}

.load-more:is(:hover, :focus) {
  color: #fff;
  background-color: #0ac963;
}

